<template>
  <div v-if="showIntro" class="intro-screen">
    <h1>Doƒüayƒ± Koru Oyunu üåø</h1>
    <p>Doƒüru se√ßimler yap, puan topla ve doƒüayƒ± mutlu et! üåç</p>
    <button @click="startGame">Ba≈üla</button>
  </div>
  <div class="quiz" v-else>
    <h2 v-if="!gameFinished">{{ currentQuestion.question }}</h2>
    
    <div v-if="!gameFinished">
      <div v-for="(option, idx) in currentQuestion.options" :key="idx">
        <button @click="selectAnswer(option.score)">
          {{ option.text }}
        </button>
      </div>
    </div>

    <div v-else>
      <h3>Skorun: {{ totalScore }} / {{ maxScore }}</h3>

      <div class="progress-bar">
        <div class="progress-fill" :style="{ width: progressPercentage + '%', backgroundColor: progressColor }"></div>
      </div>

      <p class="feedback">{{ feedbackMessage }}</p>
    </div>
  </div>
</template>

<script>
import { questionPool } from '../questions';

export default {
  components: {
   
  },
  data() {
    return {
      showIntro: true,
      selectedQuestions: [],
      currentIndex: 0,
      totalScore: 0,
      maxScore: 25, 
      gameFinished: false
    };
  },
  computed: {
    currentQuestion() {
      return this.selectedQuestions[this.currentIndex];
    },
    progressPercentage() {
      return Math.min(100, Math.round((this.totalScore / this.maxScore) * 100));
    },
    progressColor() {
      if (this.totalScore <= 5) return 'F44336'; 
      if (this.totalScore <= 15) return '#FFC107'; 
      return '#4CAF50';
    },

    feedbackMessage() {
      if (this.totalScore >= this.maxScore * 0.75) return "Harikasƒ±n! Doƒüayƒ± koruyorsun üåø";
      if (this.totalScore >= this.maxScore * 0.5) return "Fena deƒüil ama dikkatli olmalƒ±sƒ±n üåç";
      return "Karbon ayak izin y√ºksek, doƒüaya daha duyarlƒ± olmalƒ±sƒ±n üå™Ô∏è";
    }

  },
  methods: {
    startGame() {
    this.showIntro = false;
    this.shuffleAndPickQuestions();
  },
    selectAnswer(score) {
      this.totalScore += score;
      if (this.currentIndex < this.selectedQuestions.length - 1) {
        this.currentIndex++;
      } else {
        this.gameFinished = true;
      }
    },
    shuffleAndPickQuestions() {
    const shuffled = questionPool.sort(() => 0.5 - Math.random());
    this.selectedQuestions = shuffled.slice(0, 5);

    
    this.maxScore = this.selectedQuestions.reduce((sum, question) => {
      const maxOption = Math.max(...question.options.map(opt => opt.score));
      return sum + maxOption;
    }, 0);
}
  },
  created() {
    this.shuffleAndPickQuestions();
  }
};
</script>
